body, html {
    height: 100%;
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  .lesson-one {
    margin-top: 3rem; /* Adjust this if needed to avoid overlap with fixed header */
  }
  
  .hero {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 3rem); /* Adjusted height to fit within viewport considering header height */
    background-color: #2e8b57;
    background-image: url('https://img.freepik.com/premium-photo/pilot-seat-interior-spaceship_170984-19321.jpg');
    background-size: cover;
    background-position: center;
    position: relative;
  }
  
  .hero-content {
    z-index: 1;
    text-align: center;
    color: white;
  }
  
  .text {
    color: white;
    font-size: 18px;
    line-height: 1.5;
    margin: 0 20px;
  }
  
  .btn {
    margin-top: 20px;
    padding: 12px 24px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  .btn:hover {
    background-color: #0056b3;
    transform: scale(1.05);
  }
  
  #redRectangle {
    width: 90%;
    max-width: 1200px;
    height: 100px;
    background: linear-gradient(to right, #ff0000 0%, #ff0000 40%, #00ff00 40%, #00ff00 60%, #ff0000 60%, #ff0000 100%);
    position: relative;
    z-index: 1;
  }
  
  #blackBar {
    position: absolute;
    top: 0;
    width: 10px;
    height: 100%;
    background-color: #000;
    animation: moveBar 2s ease-in-out infinite;
    z-index: 2;
  }
  
  @keyframes moveBar {
    0% { left: -10px; }
    50% { left: calc(100% - 20px); }
    100% { left: -10px; }
  }
  
  #breatheButton {
    position: absolute;
    top: 30%;
    left: 44.5%;
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    z-index: 3;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  #breatheButton:hover {
    background-color: #45a049;
    transform: scale(1.05);
  }
  

  
  #counterText {
    position: absolute;
    top: 37.5%;
    left: 47.5%;
    transform: translateX(-50%);
    color: white;
    text-align: center;
    width: 200px;
    font-size: 14px;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 5px;
    border-radius: 5px;
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Dark semi-transparent background */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000; /* Ensure it appears on top */
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
  }
  
  